@import './partials/color';
@import url('https://fonts.googleapis.com/css?family=Raleway:300,300i,900');

body, html {
	height:100%;
	margin:0;
	padding:0;
}
body {
	font-family: 'Raleway', sans-serif;
	font-size:32px;
  font-weight:300;
}
header {
  max-width:960px;
  margin-left:auto;
  margin-right:auto;

  h1 {
    font-size:4rem;
  }
}
h2, h3, h4, h5 {
  margin:0;
  font-weight:900;
}
h2 {
  font-size:2.75rem;
  width:1040px;
  margin:7rem auto 0;
}
h3 {
  font-size:2rem;
}
h4, h5 {
  font-size:1.25rem;
}
iframe {
	background-color:#fff;}


/*faux center column*/
dt, .interaction-type-details, .controls {
	width:960px;
	margin:0 auto;
	padding:0 20px;
}

.controls {
  box-sizing:border-box;
  padding:0 20px;
  overflow: hidden;
  position: relative;
  margin-bottom:50px;

  ul {
    list-style-type: none;
    position: relative;
    width:960px;
    margin:0 auto;
    padding:0;
    overflow: hidden;

    li {
      margin: 15px 10px 15px 10px;
      font-family:Arial, Helvetica, sans-serif;
      font-size: 10pt;
      display: inline-block;
      width:150px;
      margin:0;
      min-height: 100px;
      vertical-align: top;
      text-align: center;
      zoom: 1;
      *display: inline;

       a {
        display:block;
        min-height:112px;
        padding:15px;
        border-radius:21px;
        -moz-border-radius:21px;
        -webkit-border-radius:21px;
        background-color:#efefef;
        border:1px solid white;

        &:hover,
        &:focus {
          border:1px solid #97cc3a;

          &.current {
            background-color:$active-example-bg;
            &:hover,
            &:focus,
            &:active {
              background-color:$active-example-bg;
            }
          }
        }

        &.current {
          background-color:$active-example-bg;
          border:1px solid #97cc3a;
        }

        img {
          display:block;
          margin:0 auto 7px auto;
        }
      }
    }
  }
}

dt {
	padding-top:10px;
	border:0px solid #fff;
	border-radius:21px 21px 0 0;
	-moz-border-radius:21px 21px 0 0;
	-webkit-border-radius:21px 21px 0 0;
}
dd {
	margin-left:0;
	margin-bottom:1.5em;
	margin-right:0;

  .interaction-type-details {
    box-sizing:border-box;
    padding:0 20px;

    ul {
      margin-top:0;
      margin-bottom:.5em;
    }

    p, li {
      margin-top:0;
      margin-bottom:0;
      padding-bottom:.5em;
      font-size:.7em;
      font-style: italic;
      color:#333;
    }

    li {
      padding-bottom:0;
      font-style:normal;
    }
  }
}

.key {
  padding:5px;
  background-color:#ccc;
  font-style:italic;
  font-size:.8em;
  font-family:monospace;

  dd, p {
    margin-bottom:0;
  }
}

a {
	color: #000000;
	text-decoration: none;
	text-align: center;
}

img {
  border:none;
}

h1 {
  margin: 0 0 25px 0;
  text-align: center;
}

.hide {
	position: absolute !important;
	top: -9999px !important;
	left: -9999px !important;
}

/*Box container for eduQuery example iframe*/
.boxy {
  margin:20px 20px 50px;
	padding:20px;
  border:20px solid #97cc3a;
  border-radius:30px;
  -moz-border-radius:30px;
  -webkit-border-radius:30px;
  text-align:center;

  h3 {
    display:inline;
    font-size:1em;
    font-weight:900;
  }

  a.close {
    display:inline-block;
    width:300px;
    margin-left:20px;
    border:2px solid #df0000;
    background-color:#df0000;
    color:#fff;

    &:hover,
    &:focus {
      background-color:#fff;
      color:#df0000;
    }
  }

  iframe {
    height:1px;
    margin-top:50px;
  }

  .link-to-text {
    a {
      border-bottom:1px solid #ccc;

      &:hover,
      &:focus {
        border-color:#000;
      }
    }
  }
}
